import React from 'react';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import { Route, withRouter, Switch } from 'react-router-dom';

/* import pages */
import { PageDev, Page404, PageError } from './pages/error';
import Home from './pages/home';

import './scss/Router.scss';

function AnimatedRouter({ location }) {
  let home = process.env.PUBLIC_URL;
  return (
    <div>
      <TransitionGroup className="route-group">
        <CSSTransition
          key={location.key}
          timeout={{ enter: 500, exit: 500 }}
          classNames="route"
        >
          <section className="route-section">
            <Switch location={location}>
              <Route path={`${home}/`} component={Home} exact />
              <Route path={`${home}/experience`} component={PageDev} exact />
              <Route path={`${home}/experience/work`} component={PageDev} exact />
              <Route path={`${home}/experience/pubs`} component={PageDev} exact />
              <Route path={`${home}/projects`} component={PageDev} exact />
              <Route path={`${home}/projects/undergrad`} component={PageDev} exact />
              <Route path={`${home}/projects/grad`} component={PageDev} exact />
              <Route path={`${home}/projects/personal`} component={PageDev} exact />
              <Route path={`${home}/blog`} component={PageDev} exact />
              <Route path={`${home}/fun`} component={PageDev} exact />
              <Route path={`${home}/cs394n`} component={PageDev} exact />
              <Route component={Page404} status={404} />
              <Route component={PageError} />
            </Switch>
          </section>
        </CSSTransition>
      </TransitionGroup>
    </div>
  )
}

export default withRouter(AnimatedRouter);
